    padding: 30px;
    width: 100%;
    gap: 0px 20px;
    height: 100%;
    box-shadow: 0px 0px 5px 0px rgb(209, 207, 207);
  }
  .university {
    display: flex;

    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    text-align: center;
    height: auto;
    box-shadow: 0px 0px 5px 0px rgb(209, 207, 207);
  }
  .forgot a {
    text-decoration: none;
    font-size: 11px;
    color: rgb(40, 155, 142);
  }
  .input-remember span {
    font-size: 11px;
